import PropTypes from 'prop-types';
import Slider from 'react-slick';
import { m } from 'framer-motion';
import { useState, useRef } from 'react';
// @mui
import { alpha, useTheme, styled } from '@mui/material/styles';
import { CardContent, Box, Card, Typography, Link } from '@mui/material';
// _mock_

// components
import Image from '../../../../components/Image';
import { varFade } from '../../../../components/animate';
import { CarouselDots, CarouselArrows } from '../../../../components/carousel';
import useAuth from '../../../../hooks/useAuth';

// ----------------------------------------------------------------------

const OverlayStyle = styled('div')(({ theme }) => ({
  top: 0,
  left: 0,
  right: 0,
  bottom: 0,
  zIndex: 8,
  position: 'absolute',
  backgroundColor: alpha(theme.palette.grey[900], 0.64),
}));

// ----------------------------------------------------------------------

// Ensure only one slide is visible to avoid vertical duplicates if base slick CSS is not applied
const StyledSlider = styled(Slider)(() => ({
  '& .slick-list': {
    overflow: 'hidden',
  },
  '& .slick-slide': {
    display: 'none',
  },
  '& .slick-slide.slick-active': {
    display: 'block',
  },
}));

export default function AppFeatured() {
  const { setting } = useAuth();
  const theme = useTheme();
  const carouselRef = useRef(null);
  const featureLength = Array.isArray(setting?.feature) ? setting.feature.length : 0;
  const [currentIndex, setCurrentIndex] = useState(theme.direction === 'rtl' ? Math.max(featureLength - 1, 0) : 0);

  const settings = {
    speed: 800,
    dots: true,
    arrows: false,
    autoplay: true,
    slidesToShow: 1,
    slidesToScroll: 1,
    rtl: Boolean(theme.direction === 'rtl'),
    beforeChange: (current, next) => setCurrentIndex(next),
    ...CarouselDots({
      zIndex: 9,
      top: 24,
      left: 24,
      position: 'absolute',
    }),
  };

  const handlePrevious = () => {
    carouselRef.current.slickPrev();
  };

  const handleNext = () => {
    carouselRef.current.slickNext();
  };
  return (
    <Card>
      <StyledSlider ref={carouselRef} {...settings}>
        {setting?.feature?.map((app, index) => (
          <CarouselItem key={app?.id} item={app} isActive={index === currentIndex} />
        ))}
      </StyledSlider>

      <CarouselArrows
        onNext={handleNext}
        onPrevious={handlePrevious}
        spacing={0}
        sx={{
          top: 16,
          right: 16,
          position: 'absolute',
          '& .arrow': {
            p: 0,
            width: 32,
            height: 32,
            opacity: 0.48,
            color: 'common.white',
            '&:hover': { color: 'common.white', opacity: 1 },
          },
        }}
      />
    </Card>
  );
}

// ----------------------------------------------------------------------

CarouselItem.propTypes = {
  isActive: PropTypes.bool,
  item: PropTypes.shape({
    description: PropTypes.string,
    image: PropTypes.string,
    link: PropTypes.any.isRequired,
    title: PropTypes.string,
  }),
};


function CarouselItem({ item, isActive }) {
  const { image, title, description, link } = item;

  return (
    <Box sx={{ position: 'relative' }}>
      <CardContent
        sx={{
          bottom: 0,
          width: 1,
          zIndex: 9,
          textAlign: 'left',
          position: 'absolute',
          color: 'common.white',
        }}
      >
        <m.div variants={varFade().inRight} animate={isActive ? "animate" : "initial"}>
          <Typography variant="overline" component="div" sx={{ mb: 1, opacity: 0.48 }}>
            Featured
          </Typography>
        </m.div>
        <m.div variants={varFade().inRight} animate={isActive ? "animate" : "initial"}>
          <Link href={link} color="inherit" underline="none">
            <Typography variant="h5" gutterBottom noWrap>
              {title}
            </Typography>
          </Link>
        </m.div>
        <m.div variants={varFade().inRight} animate={isActive ? "animate" : "initial"}>
          <Typography variant="body2" noWrap>
            {description}
          </Typography>
        </m.div>
      </CardContent>
      <OverlayStyle />
      <Image alt={title} src={image} sx={{ height: { xs: 280, xl: 320 } }} />
    </Box>
  );
}
