/* eslint-disable consistent-return */
import PropTypes from 'prop-types';
// @mui
import { Box, Card, Typography } from '@mui/material';
// utils
import { fCurrency } from '../../../../utils/formatNumber';

// ----------------------------------------------------------------------

Appbalance.propTypes = {
  title: PropTypes.string.isRequired,
  total: PropTypes.number,
};

export default function Appbalance({ title, total }) {
  // Uniform Green Gradient matching the website theme
  const gradient = (theme) => `linear-gradient(135deg, ${theme.palette.primary.main} 0%, ${theme.palette.primary.darker} 100%)`;

  return (
    <Card sx={{
      display: 'flex',
      alignItems: 'center',
      p: 3,
      background: gradient,
      color: '#fff',
      borderRadius: 2,
      boxShadow: (theme) => theme.customShadows.z4,
      position: 'relative',
      overflow: 'hidden',
      height: '100%'
    }}>
      <Box sx={{ flexGrow: 1, zIndex: 2 }}>
        <Typography variant="subtitle2" sx={{ opacity: 0.9, fontWeight: 700, letterSpacing: 1, mb: 1 }}>
          {title}
        </Typography>
        <Typography variant="h3" sx={{ fontWeight: 800 }}>
          ₦{fCurrency(total)}
        </Typography>
      </Box>

      {/* Decorative overlaid circle */}
      <Box
        sx={{
          position: 'absolute',
          top: -30,
          right: -30,
          width: 150,
          height: 150,
          borderRadius: '50%',
          background: 'rgba(255,255,255,0.1)',
          zIndex: 1
        }}
      />
    </Card>
  );
}
