import PropTypes from 'prop-types';
import { Link as RouterLink } from 'react-router-dom';
import { Card, Link, Grid, Typography } from '@mui/material';

// utils


// ----------------------------------------------------------------------

// ----------------------------------------------------------------------

AppPackages.propTypes = {
  image: PropTypes.string,
  displayname: PropTypes.string,
  link: PropTypes.any.isRequired
};


export default function AppPackages({ image, displayname, link }) {
  return (
    <Link component={RouterLink} to={link} color="inherit" underline="none">
      <Card
        sx={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          justifyContent: 'center',
          p: 4,
          height: '100%',
          textAlign: 'center',
          transition: 'all 0.3s ease-in-out',
          '&:hover': {
            transform: 'translateY(-5px)',
            boxShadow: (theme) => theme.customShadows.z24,
            borderColor: 'primary.main',
            bgcolor: 'primary.lighter'
          }
        }}
      >
        <div style={{ marginBottom: '16px', transform: 'scale(1.2)' }}>
          {image}
        </div>
        <Typography variant="subtitle1" sx={{ fontWeight: 600 }}>
          {displayname}
        </Typography>
      </Card>
    </Link>
  );
}
