import * as Yup from 'yup';
import { useSnackbar } from 'notistack';
import { yupResolver } from '@hookform/resolvers/yup';
import { useForm } from 'react-hook-form';
import { CopyToClipboard } from 'react-copy-to-clipboard';
// @mui
import { styled } from '@mui/material/styles';
import { LoadingButton } from '@mui/lab';
import { Typography, Stack, Card, CardContent, Button } from '@mui/material';
import useAuth from '../../../../hooks/useAuth';
import Iconify from '../../../../components/Iconify';
import axios from '../../../../utils/axios';
// components
import { FormProvider, RHFTextField } from '../../../../components/hook-form';

// ----------------------------------------------------------------------

const RootStyle = styled(Card)(({ theme }) => ({
  boxShadow: 'none',
  textAlign: 'center',
  backgroundColor: theme.palette.primary.lighter,
  [theme.breakpoints.up('md')]: {
    height: '100%',
    display: 'flex',
    textAlign: 'left',
    alignItems: 'center',
    justifyContent: 'space-between',
  },
}));

export default function AppReferal() {
  const { setting, user } = useAuth();
  const { enqueueSnackbar } = useSnackbar();
  const ChangePassWordSchema = Yup.object().shape({
    refemail: Yup.string().email().required('Referral Email Required'),
  });
  const AccessToken = window.localStorage.getItem('accessToken');
  const defaultValues = {
    refemail: '',
  };
  // ----------------------------------------------------------------------
  const methods = useForm({
    resolver: yupResolver(ChangePassWordSchema),
    defaultValues,
  });

  const {
    reset,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;

  const onSubmit = async (data) => {
    try {
      await axios.post(`/api/inviting/user/${AccessToken}/secure`, data);
      reset()
      enqueueSnackbar('Email Sent!');
    } catch (error) {
      console.log(error)
      enqueueSnackbar('Something Went Wrong', { variant: 'error' })
    }
  };

  return (
    <RootStyle>
      <CardContent
        sx={{
          p: { md: 0 },
          pl: { md: 5 },
          color: 'grey.800',
        }}
      >
        <Stack direction="row" alignItems="center" justifyContent="space-between" noWrap>
          <Typography variant="h4">
            Invite friends <br /> and earn
          </Typography>
          <Typography variant="h2">{setting?.setting.referral_price}%</Typography>
        </Stack>
        <Typography variant="body2" sx={{ mt: 2, mb: 3 }} noWrap>
          {` You will Earn ${setting?.setting.referral_price}% on \n the person first Deposit`}
        </Typography>
        <Typography variant="body2" sx={{ mt: 2, mb: 3, flex: 1, flexWrap: 'wrap' }} >
          {`${window.location.origin}/auth/\nregister/${user?.username}`} <br /> <CopyToClipboard text={`${window.location.origin}/auth/register/${user?.username}`} onCopy={() => enqueueSnackbar('Earning Link Copied')}><Button title="copy"><Iconify icon="codicon:copy" sx={{ width: 20, height: 20, color: 'success.main' }} /></Button></CopyToClipboard>
        </Typography>
        <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
          <Stack spacing={3} alignItems="flex-end">
            <RHFTextField name="refemail" type="email" label="Referral Email" />
            <LoadingButton type="submit" variant="contained" loading={isSubmitting}>
              Invite
            </LoadingButton>
          </Stack>
        </FormProvider>
      </CardContent>


    </RootStyle>
  );
}
