import PropTypes from 'prop-types';
import { Link as RouterLink } from 'react-router-dom';
import { styled } from '@mui/material/styles';
import { Typography, Card, Button, Box, Stack, Divider } from '@mui/material';
import Iconify from '../../../../components/Iconify';
import Label from '../../../../components/Label';
import { PATH_ADMIN, PATH_DASHBOARD } from '../../../../routes/paths';
import useAuth from '../../../../hooks/useAuth';

// Styled Components
const RootStyle = styled(Card)(({ theme }) => ({
  boxShadow: '0 8px 16px 0 rgba(0,0,0,0.1)',
  textAlign: 'center',
  backgroundColor: theme.palette.common.white,
  padding: theme.spacing(3),
  overflow: 'visible'
}));

const NotificationBar = styled(Box)(({ theme, bgcolor }) => ({
  padding: theme.spacing(1.5),
  borderRadius: theme.shape.borderRadius,
  background: bgcolor || theme.palette.primary.main,
  color: theme.palette.common.white,
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'space-between',
  marginBottom: theme.spacing(2),
  width: '100%',
  flexWrap: 'wrap',
  gap: theme.spacing(1)
}));

// PropTypes definition
UserVerified.propTypes = {
  displayName: PropTypes.string,
  bool: PropTypes.bool,
  isVerified: PropTypes.bool
};

export default function UserVerified({ displayName, bool, isVerified }) {
  const { setting } = useAuth();

  const getGreeting = () => {
    const hour = new Date().getHours();
    if (hour < 12) return 'Good Morning';
    if (hour < 18) return 'Good Afternoon';
    return 'Good Evening';
  };

  const domain = (setting?.general?.app_name || window.location.hostname).toUpperCase();
  const whatsappNumber = setting?.setting?.support_phone || '07040540018';

  return (
    <RootStyle>
      <Stack direction={{ xs: 'column', md: 'row' }} justifyContent="space-between" alignItems="center" sx={{ mb: 4 }}>
        <Typography variant="h4" sx={{ color: 'text.primary', fontWeight: 700 }}>
          {getGreeting()}, <span style={{ opacity: 0.7 }}>{displayName || 'User'}</span>
        </Typography>

        <Stack alignItems="center" spacing={1}>
          {/* Google Play Placeholder */}
          <Box
            component="img"
            src="https://upload.wikimedia.org/wikipedia/commons/7/78/Google_Play_Store_badge_EN.svg"
            sx={{ height: 40, cursor: 'pointer' }}
          />
          <Button variant="contained" color="info" size="small" sx={{ borderRadius: 20, px: 3 }}>
            Download Our APP
          </Button>
        </Stack>
      </Stack>

      {/* Hidden Sections: Virtual Account Status & Package API */}

      {/* Notification Bars */}
      <NotificationBar sx={{ background: 'linear-gradient(90deg, #01b875 0%, #5FE3A8 100%)', p: { xs: 2, md: 2.5 } }}>
        <Stack direction={{ xs: 'column', md: 'row' }} alignItems="center" spacing={2} sx={{ width: 1 }}>
          <Box sx={{ flexGrow: 1, textAlign: 'left' }}>
            <Typography variant="body2" sx={{ fontWeight: 'bold', color: 'common.white', display: 'flex', alignItems: 'center', flexWrap: 'wrap' }}>
              <span style={{ color: '#ffc107', marginRight: '8px' }}>**NEW**</span>
              Own a {domain} retailer website and retail all our services; Such as DATA, Recharge cards printing, AIRTIME and BILLS Payment.
            </Typography>
          </Box>
          <Button
            component="a"
            href={`https://wa.me/${whatsappNumber}`}
            target="_blank"
            variant="contained"
            size="small"
            sx={{
              bgcolor: 'white',
              color: 'primary.main',
              fontWeight: 700,
              px: 3,
              borderRadius: 1,
              flexShrink: 0,
              width: { xs: '100%', md: 'auto' },
              '&:hover': { bgcolor: 'grey.100' }
            }}
          >
            Click Here
          </Button>
        </Stack>
      </NotificationBar>

      <NotificationBar sx={{ background: 'linear-gradient(90deg, #018F5D 0%, #01b875 100%)', p: { xs: 2, md: 2.5 } }}>
        <Stack direction={{ xs: 'column', md: 'row' }} alignItems="center" spacing={2} sx={{ width: 1 }}>
          <Box sx={{ flexGrow: 1, textAlign: 'left' }}>
            <Typography variant="body2" sx={{ fontWeight: 'bold', color: 'common.white', display: 'flex', alignItems: 'center', flexWrap: 'wrap' }}>
              <span style={{ color: '#ffc107', marginRight: '8px' }}>**NEW**</span>
              Masu bukatar mallakar Website dinsu irin fasalin na {domain} ku danna Click Here.
            </Typography>
          </Box>
          <Button
            component="a"
            href={`https://wa.me/${whatsappNumber}`}
            target="_blank"
            variant="contained"
            size="small"
            sx={{
              bgcolor: 'white',
              color: 'primary.main',
              fontWeight: 700,
              px: 3,
              borderRadius: 1,
              flexShrink: 0,
              width: { xs: '100%', md: 'auto' },
              '&:hover': { bgcolor: 'grey.100' }
            }}
          >
            Click Here
          </Button>
        </Stack>
      </NotificationBar>

    </RootStyle>
  );
}
