import PropTypes from 'prop-types';
import { Card, Typography } from '@mui/material';
import { styled, keyframes } from '@mui/material/styles';

const scroll = keyframes`
  0% { transform: translateX(100%); }
  100% { transform: translateX(-100%); }
`;

const RootStyle = styled(Card)(({ theme }) => ({
    boxShadow: 'none',
    backgroundColor: 'transparent',
    marginBottom: theme.spacing(2),
    border: 'none',
    overflow: 'hidden',
    whiteSpace: 'nowrap',
    position: 'relative'
}));

const MarqueeText = styled(Typography)(({ theme }) => ({
    display: 'inline-block',
    animation: `${scroll} 25s linear infinite`,
    fontWeight: 700,
    fontSize: '1.1rem',
    color: theme.palette.error.main, // Red for visibility/urgency as typical for marquees
    '&:hover': {
        animationPlayState: 'paused'
    }
}));

AppMarquee.propTypes = {
    message: PropTypes.string,
};

export default function AppMarquee({ message }) {
    if (!message) return null;

    return (
        <RootStyle>
            <MarqueeText variant="h6">
                {message}
            </MarqueeText>
        </RootStyle>
    );
}
