import React, { useState, useEffect } from 'react';
import { Container, Tab, Box, Tabs, Grid, Stack, Card, Typography, Button } from '@mui/material';
import { m } from 'framer-motion';
import { useSnackbar } from 'notistack';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import swal from 'sweetalert';
import { styled } from '@mui/material/styles';
import Iconify from '../../../../components/Iconify';
import useAuth from '../../../../hooks/useAuth';
import axios from '../../../../utils/axios';

import { fCurrency } from '../../../../utils/formatNumber';
import { Upgrade } from '../bank';

const Div = styled('div')(({ theme }) => ({
  ...theme.typography.button,
  backgroundColor: theme.palette.background.paper,
  padding: theme.spacing(1),
}));

const UserUpgrade = () => {
  const { user, setting } = useAuth();
  const { enqueueSnackbar } = useSnackbar();
  const [currentTab, setCurrentTab] = useState('');
  const [banks, setBanks] = useState([]);
  const accessToken = localStorage.getItem('accessToken');

  useEffect(() => {
    // Fetching banks from the backend
    axios.get(`/api/check/banks/user/gstar/${accessToken}/secure/this/site/here`)
      .then((response) => {
        if (response.data.status === 'success') {
          setBanks(response.data.banks);
          // Set the first bank as the default selected tab
          if (response.data.banks.length > 0) {
            setCurrentTab(response.data.banks[0].name); // Set the first bank name
          }
        }
      })
      .catch((error) => console.error("Error fetching banks:", error));
  }, []);

  // Card for displaying bank details
  // Card for displaying bank details
  // Card for displaying bank details
  const BankCard = ({ bank }) => (
    <Card sx={{
      p: 3,
      background: (theme) => `linear-gradient(135deg, ${theme.palette.primary.main} 0%, ${theme.palette.primary.darker} 100%)`, // Website Green Theme
      color: '#fff',
      borderRadius: 2,
      boxShadow: (theme) => `0 10px 20px ${theme.palette.primary.main}`,
      position: 'relative',
      overflow: 'hidden'
    }}>
      {/* Decorative Circle overlay */}
      <Box sx={{ position: 'absolute', top: -10, right: -10, width: 100, height: 100, borderRadius: '50%', background: 'rgba(255,255,255,0.1)' }} />

      {bank.accountType === false ? (
        <Stack spacing={3}>
          <Box display="flex" alignItems="center">
            {/* Fallback Icon if logo not available */}
            <Iconify icon="mdi:bank-transfer" sx={{ width: 24, height: 24, mr: 1, opacity: 0.8 }} />
            <Typography variant="h6" sx={{ fontWeight: 700, letterSpacing: 0.5 }}>
              {bank.name}
            </Typography>
          </Box>

          <Box>
            <Typography variant="body2" sx={{ opacity: 0.7, mb: 0.5 }}>Account Number:</Typography>
            <Stack direction="row" alignItems="center" spacing={1}>
              <Typography variant="h3" sx={{ fontWeight: 'bold' }}>{bank.account}</Typography>
              <CopyToClipboard text={bank.account} onCopy={() => enqueueSnackbar('Copied!')}>
                <Button size="small" sx={{ minWidth: 0, p: 0.5, color: '#fff', bgcolor: 'rgba(255,255,255,0.2)' }}>
                  <Iconify icon="eva:copy-fill" />
                </Button>
              </CopyToClipboard>
            </Stack>
          </Box>

          <Box display="flex" justifyContent="space-between" alignItems="flex-end">
            <Box>
              <Typography variant="subtitle2" sx={{ fontWeight: 500 }}>
                Account Name: {setting?.general?.app_name} - {user.username}
              </Typography>
              <Typography variant="body2" sx={{ opacity: 0.7 }}>
                Bank Name: {bank.name}
              </Typography>
            </Box>

            <Box textAlign="right">
              <Typography variant="subtitle2" sx={{ fontWeight: 'bold' }}>
                {bank.charges}
              </Typography>
              <Typography variant="caption" sx={{ opacity: 0.7, display: 'block', fontSize: '0.65rem' }}>
                CHARGE
              </Typography>
            </Box>
          </Box>

          <Box sx={{ mt: 2 }}>
            <Typography variant="caption" sx={{ display: 'block', opacity: 0.8, fontWeight: 'bold', mb: 0.5 }}>
              AUTOMATED BANK TRANSFER
            </Typography>
            <Typography variant="caption" sx={{ opacity: 0.6 }}>
              Make transfer to this account to fund your wallet
            </Typography>
          </Box>
        </Stack>
      ) : (
        <Box textAlign="center" py={4} sx={{ color: '#fff' }}>
          <Iconify icon="eva:alert-triangle-fill" sx={{ width: 40, height: 40, mb: 2, opacity: 0.5 }} />
          <Typography variant="h6">Service Unavailable</Typography>
        </Box>
      )}
    </Card>
  );

  return (
    <Container>
      {banks.length > 0 && currentTab && (
        <Tabs
          value={currentTab}
          onChange={(e, newValue) => setCurrentTab(newValue)}
          variant="scrollable"
          scrollButtons="auto"
          allowScrollButtonsMobile
        >
          {banks.map((bank) => (
            <Tab key={bank.name} label={bank.name} value={bank.name} />
          ))}
        </Tabs>
      )}

      <Box sx={{ mt: 3 }}>
        <Grid container spacing={5}>
          {banks.length > 0 ? (
            banks.map((bank) => (
              bank.name === currentTab && (
                <Grid item xs={12} md={12} key={bank.name}>
                  <Stack spacing={3}>
                    <BankCard bank={bank} />
                  </Stack>
                </Grid>
              )
            ))
          ) : (
            <Grid item xs={12}>
              <Typography variant="h6" sx={{ textAlign: 'center', mt: 3 }}>
                No banks available. Please check back later.
              </Typography>
            </Grid>
          )}
        </Grid>
      </Box>

    </Container>
  );
};

// The upgrade functions and component for upgrades
const onAPI = (user) => {
  swal({
    title: "Note",
    text: "YOU MUST HAVE A WEBSITE",
    icon: "info",
    buttons: true,
    dangerMode: true,
  })
    .then((willDelete) => {
      if (willDelete) {
        swal({
          text: 'Input Your Website URL',
          content: "input",
          button: {
            text: "Proceed",
            closeModal: false,
          },
        })
          .then((value) => {
            if (value === '') {
              swal('Website Url is Required');
            } else if (!value) {
              swal('Request Cancelled');
            } else {
              axios.post(`/api/upgrade/api/user`, {
                url: value,
                username: user
              })
                .then(resp => {
                  swal('success', resp.data.message, 'success');
                })
                .catch(error => {
                  swal("Oops!", error.message || 'An Error Occurred', "error");
                });
            }
          });
      } else {
        swal("Request Cancelled");
      }
    });
};

const Habukhanupgrade = () => {
  const { user, setting } = useAuth();

  const Div = styled('div')(({ theme }) => ({
    ...theme.typography.button,
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(1),
  }));

  return (
    <>
      <Upgrade />
      <Box sx={{ p: 3, textAlign: 'center' }}>
        <Typography variant="h6" paragraph>
          Are you a developer or website owner?
        </Typography>
        <Typography variant="body2" sx={{ color: 'text.secondary', mb: 3 }}>
          Upgrade to our API package to enjoy cheaper rates and seamless integration.
        </Typography>
        <Button
          variant="contained"
          onClick={() => onAPI(user.username)}
          sx={{
            bgcolor: 'primary.main',
            '&:hover': { bgcolor: 'primary.dark' }
          }}
        >
          Upgrade to API Package
        </Button>
      </Box>
    </>
  );
};

export default function UpgradePage() {
  return (
    <Container>
      <Habukhanupgrade />  {/* Move Upgrade to the back */}
      <UserUpgrade />   {/* Display UserUpgrade after Habukhanupgrade */}
    </Container>
  );
}
