import PropTypes from 'prop-types';
import { Link as RouterLink } from 'react-router-dom';
import { styled } from '@mui/material/styles';
import { Typography, Button, Card, CardContent, Grid, Box } from '@mui/material';
import { PATH_DASHBOARD, PATH_ADMIN } from '../../../../routes/paths';
import useAuth from '../../../../hooks/useAuth';

// ----------------------------------------------------------------------

const RootStyle = styled(Card)(({ theme }) => ({
  boxShadow: 'none',
  textAlign: 'left',
  backgroundColor: theme.palette.primary.darker,
  background: `linear-gradient(135deg, ${theme.palette.primary.darker} 0%, ${theme.palette.primary.main} 100%)`,
  color: theme.palette.common.white,
  position: 'relative',
  overflow: 'hidden',
  borderRadius: theme.shape.borderRadius * 2,
}));

// ----------------------------------------------------------------------

AppWelcome.propTypes = {
  displayName: PropTypes.string,
};

export default function AppWelcome({ displayName }) {
  const { user, setting } = useAuth();
  const appName = setting?.general?.app_name || window.location.hostname;

  return (
    <RootStyle>
      <CardContent
        sx={{
          p: { xs: 3, md: 5 },
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
          flexDirection: { xs: 'column', md: 'row' }
        }}
      >
        <Box sx={{ maxWidth: { md: '70%' }, mb: { xs: 3, md: 0 } }}>
          <Typography variant="h4" gutterBottom>
            WELCOME TO {appName.toUpperCase()}
          </Typography>

          <Typography variant="body2" sx={{ opacity: 0.8, mb: 3, maxWidth: 600 }}>
            {appName} is a digital platform designed for seamless access to essential services such as mobile data, airtime recharge, recharge cards, cable TV subscriptions, and utility bill payments.
            <br /><br />
            Our system is built for speed, reliability, and convenience — allowing users to complete transactions easily, anytime and anywhere.
          </Typography>
        </Box>

        <Box sx={{ display: 'flex', gap: 2, flexDirection: { xs: 'column', sm: 'row' } }}>
          {/* Admin Dashboard Button */}
          {user?.type === 'ADMIN' && (
            <Button
              component={RouterLink}
              to={PATH_ADMIN.general.app}
              variant="contained"
              sx={{
                borderRadius: 30,
                px: 4,
                py: 1.5,
                fontWeight: 'bold',
                bgcolor: 'white',
                color: 'primary.main',
                boxShadow: '0 8px 16px 0 rgba(0,0,0,0.2)',
                '&:hover': {
                  bgcolor: 'grey.100',
                }
              }}
            >
              Admin Dashboard
            </Button>
          )}

          <Button
            component={RouterLink}
            to={PATH_DASHBOARD.fund.root}
            variant="contained"
            color="secondary"
            size="large"
            sx={{
              borderRadius: 30,
              px: 4,
              py: 1.5,
              fontWeight: 'bold',
              boxShadow: '0 8px 16px 0 rgba(0,0,0,0.2)'
            }}
          >
            Fund Wallet
          </Button>
        </Box>
      </CardContent>
    </RootStyle>
  );
}
